.wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 100px;
  padding: 9px 16px;
  height: 36px;
  font-size: 1.6rem;
  font-weight: 700;
  border-radius: 4px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid transparent;
  user-select: none;

  + .wrapper {
    margin-left: 16px;
  }

  &.disabled {
    pointer-events: none !important;
    opacity: 0.5;
  }
}
// button type
.primary {
  color: rgba(255, 255, 255, 1);
  background-color: rgba(254, 44, 85, 1);
  &:hover {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)),
      #fe2c55;
  }
}

.outline {
  border-color: currentColor;
  color: var(--primary-color);
  &:hover {
    background-color: rgba(254, 44, 85, 0.06);
  }
}

.text {
  &:hover {
    text-decoration: underline;
  }
}

.rounded {
  border-radius: 999px;
  color: rgba(22, 24, 35, 0.75);
  font-weight: 700;
  color: var(--black);
  font-size: 14px;
  min-width: 90px;
  height: 32px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 16%);
  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(255, 255, 255, 1);
  }
}

.icon + .title, .title + .icon {
  margin-left: 8px;
}

.icon{
  display: inline-block ;
  width: 20px;
  text-align: center;
}

//button styles

.small {
  min-width: 88px;
  padding: 4px 16px;
}

.large {
  min-width: 148px;
  padding: 4px 16px;
}
