.wapper {
    --search-border-radius : 92px;
    --search-height :46px;
    --search-spacer :9px;
    --search-btn-width : 62px;
    width: 100%;
    height: var(--default-layout-header-height);
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    display: flex;
    justify-content: center;
}

.inner{
    height: 100%;
    padding: 0 24px;
    width: var(--default-layout-width);
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.logo{
    display: flex;
    align-items: center;
}

.search-result, .search {
    width: 361px;
}

.search{
    padding-left: 16px;
    height: var(--search-height);
    background-color: rgb(22, 24, 35, 0.06);
    border-radius: var(--search-border-radius);
    display: flex;
    position: relative;
    border: 1.5px solid transparent;

    input{
        height: 100%;
        padding-left: 16px;
        color: var(--black);
        font-size: 1.6rem;
        flex: 1;
        border: none;
        background-color: transparent;
        outline: none;
        border: none;
        caret-color: var(--primary-color);
    }

    &::after {
        content: "";
        position: absolute;
        height: calc(var(--search-height) - var(--search-spacer)*2 );
        width: 1px;
        top: var(--search-spacer);
        right: var(--search-btn-width);
        background-color: rgba(22, 24, 35, 0.13);
        
    }

    &:focus-within{
        border: 1.5px solid rgba(22, 24, 35, 0.2);
    }

    input:not(:placeholder-shown) ~ .search-btn{
        color: rgba(22, 24, 35, 0.75);
    }
}

.search-title{
    color: rgba(22,24,35,0.5);
    font-size: 1.4rem;
    font-weight: 500;
    padding: 5px 12px;
}

.clear,
.loading{
    position: absolute;
    right: calc(var(--search-btn-width) + 16px);
    top: 50%;
    transform: translateY(-50%);
    color: rgba(22, 24, 35, 0.34);
}

.search-btn{
    width: var(--search-btn-width);
    height: 100%;
    border: none;
    // padding-left: 26px;
    outline: none;
    border-top-right-radius: var(--search-border-radius);
    border-bottom-right-radius: var(--search-border-radius);
    font-size: 1.8rem;
    color: rgba(22, 24, 35, 0.34);

    &:hover{
        cursor: pointer;
        background-color: rgba(22, 24, 35, 0.03);
    }

    &:active{
        background-color: rgba(22, 24, 35, 0.06);
    }

}

.actions{
    display: flex;
    align-items: center;
}

.more-btn {
    font-size: 2rem;
    margin-left: 28px;
    background: transparent;
    padding: 4px 8px;
    cursor: pointer;
}

.action-btn{
    color :#161823 ;
    background: transparent;
    font-size: 2.4rem;
    padding: 4px 12px;
    cursor: pointer;
}
